.goals {
  width: 100%;
  margin: 0 auto;

  @media (min-width: $desktop-width) {
    width: $desktop-work-width;
    margin: 0 auto;
  }
}

.goals__list {
  list-style: none;
  padding: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $tablet-width) {
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    margin-top: 80px;
    margin-bottom: 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.goals__item {
  background-color: $color-gray;
  margin: 20px;
  padding-bottom: 22px;

  @media (min-width: $tablet-width) {
    width: auto;
    margin-bottom: 30px;
    margin-top: 0;
    margin-left: 24px;
    margin-right: 24px;
    padding-bottom: 63px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    margin-bottom: 0;
    margin-left: 0;
    margin-right: 0;
  }

  &--pro {
    .goals__description {
      @media (min-width: $tablet-width) {
        margin-right: 325px;
      }

      @media (min-width: $desktop-width) {
        margin-right: 53px;
      }
    }

    .goals__title::after {
      width: 67px;
      height: 28px;
      top: 31px;
      left: 13px;
      background-image: url("../img/svg/icon-cat-pro.svg");

      @media (min-width: $tablet-width) {
        width: 273px;
        height: 113px;
        top: 136px;
        right: 28px;
        left: auto;
      }

      @media (min-width: $desktop-width) {
        width: 134px;
        height: 57px;
        top: 68px;
        left: 34px;
      }
    }
  }

  &--slim {
    .goals__title::after {
      background-image: url("../img/svg/icon-cat-slim.svg");

      @media (min-width: $tablet-width) {
        width: 141px;
        height: 195px;
      }

      @media (min-width: $desktop-width) {
        width: 100px;
        height: 100px;
        top: 40px;
        left: 51px;
      }
    }
  }
}

.goals__title {
  font-family: $font-stack-oswald;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
  color: $text-color-main;
  padding-top: 26px;
  padding-left: 90px;
  position: relative;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 37px;
    padding-top: 77px;
    padding-left: 54px;
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    padding-left: 214px;
  }

  &::before {
    content: "";

    position: absolute;
    top: 22px;
    left: 22px;
    z-index: 1;

    width: 50px;
    height: 50px;

    background-repeat: no-repeat;
    background-position: center center;
    background-color: $color-green;
    border-radius: 50%;

    @media (min-width: $tablet-width) {
      top: 94px;
      right: 64px;
      left: auto;

      width: 202px;
      height: 202px;
    }

    @media (min-width: $desktop-width) {
      top: 45px;
      left: 52px;
      width: 100px;
      height: 100px;
    }
  }

  &::after {
    content: "";

    position: absolute;
    top: 18px;
    left: 22px;
    z-index: 1;

    width: 50px;
    height: 50px;

    background-repeat: no-repeat;
    background-position: center center;

    @media (min-width: $tablet-width) {
      top: 82px;
      right: 94px;
      left: auto;
    }

    @media (min-width: $desktop-width) {
      top: 45px;
      left: 52px;
      width: 100px;
      height: 100px;
    }
  }
}

.goals__link {
  font-family: $font-stack-oswald;
  font-size: 16px;
  line-height: 16px;
  color: $text-color-main;
  margin-left: 20px;
  display: block;
  position: relative;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
    margin-left: 54px;
  }

  &::after {
    content: "";
    position: absolute;
    left: 95px;
    top: 3px;
    // margin-top: 3px;
    // margin-left: 10px;
    width: 25px;
    height: 12px;
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url("../img/svg/icon-right-arrow-long.svg");

    @media (min-width: $tablet-width) {
      left: 129px;
      top: 5px;
    }
  }

  &:focus,
  &:hover {
    &::after {
      background-image: url("../img/svg/icon-right-arrow-verylong.svg");
      width: 34px;
    }
  }

  &:active {
    opacity: 0.3;

    &::after {
      width: 34px;
    }
  }
}

.goals__description {
  margin-bottom: 18px;
  margin-top: 30px;
  margin-right: 20px;
  margin-left: 20px;
  padding-bottom: 22px;
  border-bottom: 1px solid $color-border-gray;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 24px;
    padding: 0;
    margin-left: 53px;
    margin-top: 27px;
    margin-bottom: 56px;
    margin-right: 285px;
    border: none;
  }

  @media (min-width: $desktop-width) {
    margin-top: 74px;
    margin-right: 53px;
    margin-bottom: 29px;
  }
}
